<template>
  <body :style="{background: hsl}">
    <div class="app-wrap">
      <header>
        <input
          @keypress.enter="getResults"
          v-model="query"
          type="text"
          autocomplete="off"
          class="search-box"
          placeholder="Search for a city..."
        />
      </header>
      <main>
        <section class="location">
          <div class="city">{{ city }}</div>
          <div class="date">{{ date }}</div>
        </section>
        <div class="current">
          <div class="temp">
            {{ temp }}
            <p
              @click="
                changeColor();
                convertTempScale();
              "
              :class="{ 'not-chosen': cIsNotChosen }"
            >
              °C
            </p>
            <p>|</p>
            <p
              @click="
                changeColor();
                convertTempScale();
              "
              :class="{ 'not-chosen': fIsNotChosen }"
            >
              °F
            </p>
          </div>
          <div class="weather">{{ forecast }}</div>
          <div class="icon">
            <img :src="require(`./icons/${icon}.png`)" />
          </div>
        </div>
      </main>
    </div>
  </body>
</template>

<script>
export default {
  data() {
    return {
      api: {
        key: "6f4a199891fd6b43ed1147f36db2b824",
        base: "https://api.openweathermap.org/data/2.5/",
      },
      query: "",

      city: "Bruce Grove",
      temp: 666,
      forecast: "Hot as Hell",
      date: `${new Date().toString().slice(0, 4)} ${new Date()
        .toString()
        .slice(15, 21)}`,
      icon: "01d",
      hsl: "hsl(0, 84%, 52%)",

      cIsNotChosen: false,
      fIsNotChosen: true,
    };
  },
  methods: {
    getResults() {
      fetch(
        `${this.api.base}weather?q=${this.query}&units=metric&APPID=${this.api.key}`
      )
        .then((response) => {
          if (response.ok) {
            return response.json();
          }
        })
        .then((data) => {
          this.parseObj(data);
        });
    },
    parseObj(weatherObj) {
      if (weatherObj.sys.country === undefined) {
        this.city = `${weatherObj.name}`
      } else {
        this.city = `${weatherObj.name}, ${weatherObj.sys.country}`;
      }
      this.temp = Math.floor(weatherObj.main.temp);
      this.forecast = weatherObj.weather[0].main;
      this.icon = weatherObj.weather[0].icon;
      console.log(weatherObj)
      this.setBackground();
    },
    setBackground() {
      let f = Math.round(this.temp * (9 / 5) + 32);
      let hue = 120 - f
      let hsl = `hsl(${hue}, 84%, 52%)`;
      this.hsl = hsl
      console.log(hsl)
    },
    changeColor() {
      this.cIsNotChosen = !this.cIsNotChosen;
      this.fIsNotChosen = !this.fIsNotChosen;
    },
    convertTempScale() {
      if (this.cIsNotChosen === true) {
        this.temp = Math.round(this.temp * (9 / 5) + 32);
      } else if (this.fIsNotChosen === true) {
        this.temp = Math.floor((this.temp - 32) * (5 / 9));
      }
    },
  },
};
</script>


